@import "../../styles/variables";

.ask {
  background-color: black;
  text-align: center;
  color: white;
  font-size: 2em;
  font-family: $font-family-title, sans-serif;
  line-height: 1.5em;
  padding: 50px 0 10px;

  &__title {
    font-size: 1em;
  }

  &__box {
    padding-top: 20px;
    display: flex;
    // height: 65vh;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  &__image {
    position: absolute;
    height: 290px;
    top: 50%;
    transform: translateY(-50%);
    left: 10%;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__form {
    flex: 0 1 50%;
  }

  .form {

    &__label {
      font-size: 0.8em;
      text-align: center;
      font-family: $font-family-subtitle;
      font-weight: 400;
      line-height: 1.5em;
    }

    &__input {
      input, textarea {
        width: 60%;
        font-size: 0.7em;
        padding: 5px 7px;
        border-radius: 5px;
        margin-bottom: 10px;
      }
      textarea {
        margin-top: 10px;
        resize: none;
      }
    }

    &__btn {
      margin: 30px 0;
    }
  }

  input[type='number'] {
    -moz-appearance:textfield;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
      -webkit-appearance: none;
  }
}

@media (max-width: 1060px) {
  .ask {
    font-size: 1.8em;

    &__title {
      font-size: 0.9em;
    }
  
    &__box {
      height: auto;
    }
  
    &__image {
      height: 200px;
      top: 40%;
      left: 7%;
    }
  }
}

@media (max-width: 700px) {
  .ask {
    padding: 60px 0 10px;
  
    &__title {
      font-size: 0.7em;
    }
  
    &__box {
      display: block;
    }
  
    &__image {
      position: relative;
      height: 150px;
      top: 0;
      transform: translateY(0);
      left: 0;
      margin-bottom: 15px;
    }
  
    .form {
      &__label {
        font-size: 0.7em;
        line-height: 1.2em;
      }

      &__input {

        input, textarea {
          width: 90%;
          max-width: 350px;
        }
      }
  
      &__btn {
        margin: 20px 0;
      }
    }
  }
}
